@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mukta', sans-serif;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: rgb(46, 45, 45); */
  background: url(/static/media/signIn.7f3e8c69.jpg) no-repeat center;
  background-size: cover;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width:480px) {
  body{
      background-size: auto 100%;
  }
  
}
.App{
  width: 100%;
  height: 100vh;
}

@media only screen and (max-width:480px) {
    .Global-button{
        margin-top: 10px;
        width: 100%;
    }

}
.SignIn-inputs{
    width: 100%;
    margin-top: 14px;
    margin-bottom: 10px;

}
.SignUp-container{
    margin: auto;
    position: relative;
    top: 60px;
    padding: 12px;
    width: 400px;
    min-height: 240px;
    text-align: center;
    /* background-color: rgba(255, 255, 255, 0.3); */
    border-top-right-radius: 16px;
    border-bottom-left-radius: 16px;
    padding-bottom: 20px;
    /* border: 2px solid #6C63FF; */
}
.SignUp-container .SignUp-head{
    width: 100%;
    padding: 8px;
    font-size: 18px;
}
.SignUp-container .SignUp-body{
    width: 100%;
    height: 100%;
    padding: 8px;
}
.SignUp-container .SignUp-body .bottomHandler{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}
.SignUp-container .SignUp-body .bottomHandler p{
    display: block;
    text-align: left;
    margin-left: 1px;
}
.SignUp-container .SignUp-body .bottomHandlerCheckbox{
    display: block;
    width: 100%;
    text-align: left;
    /* margin-top: 8px; */
}
.SignUp-container .SignUp-body .bottomHandlerCheckbox label{
   position: relative;
   top: 1px;
   font-size: 16px;
}
.SignUp-alignment{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;

}
.SignUp-alignment .SignUp-flex-div-one, .SignUp-alignment .SignUp-flex-div-two{

    width: 170px;
}
@media only screen and (max-width:480px) {
    .SignUp-container{
        top: 20px;
        width: 98%;
        background-color: transparent;
        border: none;
    }
    .SignUp-container .SignUp-body .bottomHandler, .SignUp-alignment
        {
        flex-direction: column;
    }
    .SignUp-alignment .SignUp-flex-div-one, .SignUp-alignment .SignUp-flex-div-two{
        width: 100%;
    }

}
