}
.SignIn-container .SignIn-body .bottomHandler{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}
.SignIn-container .SignIn-body .bottomHandler p{
    display: block;
    text-align: left;
    margin-left: 1px;
}
.SignIn-container .SignIn-body .bottomHandlerCheckbox{
    display: block;
    width: 100%;
    text-align: left;
    /* margin-top: 8px; */
}
.SignIn-container .SignIn-body .bottomHandlerCheckbox label{
   position: relative;
